@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  background-color: rgb(246, 247, 248);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 40px;
  flex-wrap: wrap;
}
.logo {
  color: #3498db;
}
.nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav ul li {
  padding: 10px;
}
.nav ul li:hover {
  background-color: #eee;
  border-radius: 4px;
}

.link-item {
  text-decoration: none;
  font-size: 20px;
  color: #3498db;
}
.hamburger {
  background-color: transparent;
  color: #3498db;
  border: 0;
  font-size: 30px;
  display: none;
}
.hamburger:focus {
  outline: none;
}
.sec-title {
  color: #374054;
  margin-bottom: 50px;
}
@media (max-width: 600px) {
  .nav {
    justify-content: flex-end;
  }
  .nav ul {
    display: none;
  }
  .hamburger {
    display: block;
  }
  .nav ul.show {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}

.landing {
  position: relative;
  background: url(/portfolio/static/media/lead-bg.0a5a1764.jpg);
  background-size: cover;
  padding: 250px 15px;
  z-index: 0;
}
.landing-content {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.landing-content h1 {
  color: #fff;
  font-weight: 900;
  font-size: 5em;
  letter-spacing: 0.05em;
  line-height: 0.9em;
}
.landing-content h2 {
  color: #a0cfee;
  font-weight: 500;
  font-size: 2.25em;
  margin-bottom: 15px;
}
.btn {
  color: #fff;
  padding: 15px 25px;
  background-color: rgba(52, 152, 219, 0.8);
  border: 3px solid #fff;
  text-decoration: none;

  border-radius: 30px;
}

.btn:hover {
  background-color: #eee;
  color: #3498db;
}
.landing-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(52, 152, 219, 0.8);
  z-index: 5;
}

.boutme-section {
  padding: 75px 15px;

  border-bottom: 1px solid #dcd9d9;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.about-me {
  flex: 1 1 40%;
  text-align: center;
  font-size: 4em;
  font-weight: 400;
}
.bout-meP {
  flex: 3 0 60%;
}
.boutme-text {
  font-size: 20px;
  font-weight: 200;
  line-height: 35px;
}
.Contact-Container {
  border-bottom: 1px solid #dcd9d9;

  margin: 0 auto;
  text-align: center;
}
.infos-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.infos {
  color: #74808a;
  font-size: 32px;
  padding: 25px;
}
.infos:hover {
  color: #3498db;
}
@media (max-width: 800px) {
  .about-me {
    flex: 1 1 100%;
    padding-bottom: 20px;
  }

  .bout-meP {
    flex: 1 1 100%;
  }
}

.education-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 15px 20px 15px;
  border-bottom: 1px solid #dcd9d9;
}

.education-block {
  max-width: 700px;
  border: 1px solid #dcd9d9;
  padding: 15px;
  margin: 15px;
}
.education-block h3,
.education-block h4 {
  color: #374054;
  font-weight: 500;
}
.education-block h4 {
  margin-bottom: 15px;
}
.education-block p {
  color: #374054;
  font-size: 15px;
}

.skills {
  padding: 50px 15px;
  text-align: center;
}
.skills ul {
  display: block;
  margin: 0 auto;
  padding: 0;
  max-width: 800px;
  list-style: none;
}
.skills li {
  display: inline-block;
  margin: 7px;
  padding: 5px 10px;
  color: #374054;
  background: #e4e4ea;
  list-style: none;
  cursor: default;
  font-size: 25px;
}
